.user {
  position: absolute;
  top: 4.2rem;
  right: 4.2rem;
  background-color: var(--color-dark--1);
  padding: 1rem 1.4rem;
  border-radius: 7px;
  z-index: 999;
  box-shadow: 0 0.8rem 2.4rem rgba(36, 42, 46, 0.5);
  font-size: 1.6rem;
  font-weight: 600;

  display: flex;
  align-items: center;
  gap: 1.6rem;
}

.user img {
  border-radius: 100px;
  height: 4rem;
}

.user button {
  background-color: var(--color-dark--2);
  border-radius: 7px;
  border: none;
  padding: 0.6rem 1.2rem;
  color: inherit;
  font-family: inherit;
  font-size: 1.2rem;
  font-weight: 700;
  text-transform: uppercase;
  cursor: pointer;
}

.text {
  margin-right: 5px;
}

@property --k {
  syntax: '<number>';
  initial-value: 0;
  inherits: false;
}

.textName {
  --k: 0;
  place-self: center;
  background:
          linear-gradient(90deg,
          hsl(calc(var(--k)*1turn), 95%, 65%),
          hsl(calc(var(--k)*1turn + 90deg), 95%, 65%)) text;
  color: transparent;
  filter: url(#f);
  text-align: center;
  animation: k 4s linear infinite
}

@keyframes k {
  to {
    --k: 1;
  }
}

